import React, {Suspense, lazy} from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {FallbackView} from '../../_metronic/partials'
import {FaqScreens} from '../modules/faq/Screens'

export function PrivateRoutes() {
  const ProfileRoutes = lazy(() => import('../modules/profile/ProfileRoutes'))
  const RoleManagementPage = lazy(() => import('../modules/role-management/RoleManagementRoutes'))
  const Faq = lazy(() => import('../modules/faq/FaqRoutes'))
  const MemberRoutes = lazy(() => import('../modules/member-account/MemberRoutes'))
  const UserRoutes = lazy(() => import('../modules/user/UserRoutes'))
  const GenderRoutes = lazy(() => import('../modules/gender/GenderRoutes'))
  const DatingPlaceRoutes = lazy(() => import('../modules/dating-place/DatingPlaceRoutes'))
  const EducationRoutes = lazy(() => import('../modules/education/EducationRoutes'))
  const SchoolRoutes = lazy(() => import('../modules/school/SchoolRoutes'))
  const ReligionRoutes = lazy(() => import('../modules/religion/ReligionRoutes'))
  const LanguageRoutes = lazy(() => import('../modules/language/LanguageRoutes'))
  const SportRoutes = lazy(() => import('../modules/sport/SportRoutes'))
  const PetsRoutes = lazy(() => import('../modules/pets/PetsRoutes'))
  const TravellerTypeRoutes = lazy(() => import('../modules/traveller-type/TravellerTypeRoutes'))
  const MusicGenreRoutes = lazy(() => import('../modules/music-genre/MusicGenreRoutes'))
  const MusicArtistRoutes = lazy(() => import('../modules/music-artist/MusicArtistRoutes'))
  const RelationshipPreferenceRoutes = lazy(
    () => import('../modules/relationshipPreference/RelationshipPreferenceRoutes')
  )
  const RelationshipTypeRoutes = lazy(
    () => import('../modules/relationshipType/RelationshipTypeRoutes')
  )
  const ManageAdminRoutes = lazy(() => import('../modules/manage-admin/ManageAdminRoutes'))
  const GameTypeRoutes = lazy(() => import('../modules/game-type/GameTypeRoutes'))
  const GamesRoutes = lazy(() => import('../modules/games/GamesRoutes'))
  
  return (
    <Suspense fallback={<FallbackView />}>
      <Switch>
        <Route path='/profile' component={ProfileRoutes} />
        <Route path='/role-management' component={RoleManagementPage} />
        <Route path={FaqScreens.FAQ.PATH} component={Faq} />
        <Redirect from='/auth' to='/' />
        <Redirect exact from='/' to='/' />
        <Route path='/verified' component={MemberRoutes} />
        <Route path='/unverified' component={MemberRoutes} />
        <Route path='/user' component={UserRoutes} />
        <Route path='/gender' component={GenderRoutes} />
        <Route path='/datingPlace' component={DatingPlaceRoutes} />
        <Route path='/education' component={EducationRoutes} />
        <Route path='/school' component={SchoolRoutes} />
        <Route path='/religion' component={ReligionRoutes} />
        <Route path='/language' component={LanguageRoutes} />
        <Route path='/sport' component={SportRoutes} />
        <Route path='/pets' component={PetsRoutes} />
        <Route path='/travellerType' component={TravellerTypeRoutes} />
        <Route path='/musicGenre' component={MusicGenreRoutes} />
        <Route path='/musicArtist' component={MusicArtistRoutes} />
        <Route path='/preference' component={RelationshipPreferenceRoutes} />
        <Route path='/relationshipType' component={RelationshipTypeRoutes} />
        <Route path='/gameType' component={GameTypeRoutes} />
        <Route path='/games' component={GamesRoutes} />
        
        <Route path='/manage-admin' component={ManageAdminRoutes} />
        <Redirect to='error/404' />
      </Switch>
    </Suspense>
  )
}
